import axios from 'axios';
import router from "@/router";
import Element from "element-ui";
import {serverIp, serverPort} from "../public/config";

axios.defaults.baseURL = `http://${serverIp}:${serverPort}`
//请求配置
const request = axios.create({
    timeout: 50000,
    baseURL : `http://${serverIp}:${serverPort}`,
    headers: {
        'Content-Type': "application/json; charset=utf-8"
    }
})


//请求拦截
request.interceptors.request.use(config => {
    //附带请求头（token）
    config.headers['Authorization'] = localStorage.getItem("token")
    // config.headers['Content-Security-Policy'] = 'default-src http: https:'
    // config.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
    return config
})

//结果拦截
request.interceptors.response.use(
    //有返回结果
    response => {


        let res = response.data

        //成功
        if (res.code === 200||res.code === 0) {
            return response
        }
        else if(res.code==501)
        {
            //异常
            Element.Message.error(res.msg)
            localStorage.clear()
            sessionStorage.clear()
            this.$store.commit("resetState")
            //回到登录页
            // this.push("/login")

            return Promise.reject(response.data.msg)

        }
        else {
            //异常
            Element.Message.error(!res.msg ? '系统异常' : res.msg)
            return Promise.reject(response.data.msg)
        }
    },
    //中间业务异常
    error => {
        // if(!error.response)
        // {
        //     return Promise.reject(error)
        //
        // }

        if (error.response.data) {
            error.message = error.response.data.msg
        }
        if (!error.message) {
            error.message = "系统异常"
        }

        // //没有权限，还没有登录，跳到登录页
        // if (error.response.status === 400&&localStorage.getItem("userInfo")===null) {
        //     // router.push("/login")
        //     error.message="你还没有登录，请登录你的账号！"
        //     router.push("/login")
        // }

        //弹窗
        Element.Message.error(error.message, {duration: 3000})
        return Promise.reject(error)
    }
)

export default request

