import Vue from 'vue'
import Vuex from 'vuex'

import menus from "@/store/modules/menus";
import {serverIp, serverPort, webIp, webPort} from "../../public/config";

Vue.use(Vuex)

export const BaseRequestURL=`http://${serverIp}:${serverPort}`
export const BaseWebURL=`http://${webIp}:${webPort}`

export default new Vuex.Store({
  state: {
    /////////////////////////////////图片上传与下载/////////////////////////////////////
    baseResultDownloadURL : BaseRequestURL+'/model-file/download/',


  },
  getters: {
  },
  mutations: {

    SET_TOKEN:(state ,token)=>{
      state.token=token
      localStorage.setItem("token",token)
    },
    SET_USER_INFO:(state ,userInfo)=>{
      state.userInfo=userInfo
      localStorage.setItem("userInfo",userInfo)
    },

  },
  actions: {
  },
  modules: {
    menus
  }
})
